.m-lines {
  width: 40px;
  height: 40px;
  position: relative;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  display: none; }
  .m-lines.open .two {
    opacity: 0; }
  .m-lines.open .one {
    -webkit-transform: rotate(45deg) translate(3px, 3px);
    transform: rotate(45deg) translate(3px, 3px);
    background-color: #000;
    width: 22px; }
  .m-lines.open .three {
    -webkit-transform: rotate(-45deg) translate(4px, -4px);
    transform: rotate(-45deg) translate(4px, -4px);
    background-color: #000;
    width: 22px; }
  .m-lines > div {
    height: 16px; }
    .m-lines > div > div {
      width: 18px;
      height: 2px;
      background: #000;
      border-radius: 1px;
      margin: 3px auto;
