.Help {
  background: #e5e5e5;
  width: 100%;

  .Help-Cont {
    max-width: 1016px;
    display: flex;
    flex-direction: column;

    &-Title {
      padding-top: 100px;
      font-style: normal;
      font-weight: bold;
      font-size: 42px;
      line-height: 48px;
      text-align: center;
      color: #161616;
    }

    &-Wrap {
      display: flex;
      padding-bottom: 100px;

      &-Menu {
        width: 300px;
        max-height: 171px;
        margin-top: 50px;
        background-color: #fff;
        box-shadow: 0px 8px 32px rgba(22, 22, 22, 0.05);
        border-radius: 4px;

        p {
          padding: 19px 24px;
          font-style: normal;
          font-weight: 600;
          font-size: 15px;
          line-height: 18px;
          color: rgba(22, 22, 22, 0.48);
        }
      }

      &-Context {
        //   border: 1px solid green;
        margin-top: 50px;
        margin-bottom: 100px;
          width: 100%;
          margin-left: 24px;
        //   height: 100px;
      }
    }
  }
}

.isActive {
    color: #F58436 !important;
    border-left: 1px solid #F58436;
}
