.Terms {
  width: 100%;
  background: #e5e5e5;

  &-Cont {
    padding: 100px 50px;
    display: flex;
      flex-direction: column;
      align-items: center;

    &-Context {
      width: 100%;
      max-width: 810px;
      display: flex;
      flex-direction: column;
      // align-items: center;
    }

    h1 {
      font-style: normal;
      font-weight: bold;
      font-size: 42px;
      line-height: 48px;
      text-align: center;
      color: #161616;
      margin-bottom: 50px;
    }

    h2 {
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 24px;
      color: #161616;
      margin-bottom: 24px;
    }

    h4 {
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 24px;
      color: #161616;
      margin-bottom: 24px;
    }

    p {
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 22px;
      color: rgba(22, 22, 22, 0.74);
      margin-bottom: 24px;
    }
  }
}
